import React from 'react';
import { Button, Tooltip } from 'antd';
import { StarOutlined, StarFilled } from '@ant-design/icons';
import { message } from 'antd';
import { addFavoriteItem, deleteFavoriteItem } from '../utils';
import './Home.css';

const CardTitle = ({ item, loggedIn, favs = [], favOnChange }) => {
  const title = `${item.broadcaster_name} - ${item.title}`;
  const isFav = favs.find((fav) => fav.twitch_id === item.twitch_id);

  const favOnClick = () => {
    if (isFav) {
      deleteFavoriteItem(item)
        .then(() => favOnChange())
        .catch((err) => message.error(err.message));
      return;
    }
    addFavoriteItem(item)
      .then(() => favOnChange())
      .catch((err) => message.error(err.message));
  };

  return (
    <div className="title-container">
      {loggedIn && (
        <Tooltip 
          title={isFav ? "Remove from favorite list" : "Add to favorite list"}
          overlayStyle={{ 
            maxWidth: 'none',
            whiteSpace: 'nowrap',
            zIndex: 9999
          }}
          placement="top"
          arrow={false}
          getPopupContainer={() => document.body}
        >
          <Button 
            shape="circle" 
            icon={isFav ? <StarFilled /> : <StarOutlined />} 
            onClick={favOnClick} 
          />
        </Tooltip>
      )}
      <Tooltip 
        title={title}
        overlayStyle={{ 
          maxWidth: 'none',
          whiteSpace: 'nowrap',
          zIndex: 9999
        }}
        placement="top"
        arrow={false}
        getPopupContainer={() => document.body}
      >
        <div className="title-text">
          {title}
        </div>
      </Tooltip>
    </div>
  );
};

export default CardTitle;