import { Button, Form, Input, message, Modal, Progress } from 'antd';
import React, { useState } from 'react';
import { LockOutlined, UserOutlined } from '@ant-design/icons';
import { register } from '../utils';

function Register() {
  const [displayModal, setDisplayModal] = useState(false);
  const [password, setPassword] = useState('');
  const [passwordStrength, setPasswordStrength] = useState(0);

  const handleCancel = () => {
    setDisplayModal(false);
  };

  const signupOnClick = () => {
    setDisplayModal(true);
  };

  const checkPasswordStrength = (value) => {
    let strength = 0;
    
    // Length check
    if (value.length >= 8) strength += 1;
    if (value.length >= 12) strength += 1;
    
    // Complexity checks
    if (/[A-Z]/.test(value)) strength += 1;         // Uppercase
    if (/[a-z]/.test(value)) strength += 1;         // Lowercase
    if (/\d/.test(value)) strength += 1;            // Number
    if (/[^A-Za-z0-9]/.test(value)) strength += 1;  // Special char
    
    setPasswordStrength((strength / 6) * 100); // Convert to percentage
  };

  const onFinish = (data) => {
    if (data.password !== data.confirmPassword) {
      message.error('Passwords do not match!');
      return;
    }

    register(data)
      .then(() => {
        setDisplayModal(false);
        message.success('Successfully signed up');
      })
      .catch((err) => {
        message.error(err.message);
      });
  };

  const passwordStatus = () => {
    if (passwordStrength >= 80) return 'strong';
    if (passwordStrength >= 50) return 'medium';
    return 'weak';
  };

  const passwordProgress = () => {
    const status = passwordStatus();
    const colors = {
      weak: '#ff0000',
      medium: '#ff9900',
      strong: '#00cc30',
    };
    return (
      <div style={{ marginBottom: 16 }}>
        <Progress
          percent={passwordStrength}
          strokeColor={colors[status]}
          showInfo={false}
        />
        <div style={{ color: colors[status] }}>
          Password Strength: {status}
        </div>
      </div>
    );
  };

  return (
    <>
      <Button shape="round" type="primary" onClick={signupOnClick}>
        Register
      </Button>
      <Modal
        title="Register"
        visible={displayModal}
        onCancel={handleCancel}
        footer={null}
        destroyOnClose={true}
      >
        <Form
          name="normal_register"
          initialValues={{ remember: true }}
          onFinish={onFinish}
          preserve={false}
        >
          <Form.Item
            name="username"
            rules={[{ required: true, message: 'Please input your Username!' }]}
          >
            <Input prefix={<UserOutlined />} placeholder="Username" />
          </Form.Item>

          <Form.Item
            name="password"
            rules={[
              { required: true, message: 'Please input your Password!' },
              { min: 8, message: 'Password must be at least 8 characters!' },
            ]}
          >
            <Input.Password
              prefix={<LockOutlined />}
              placeholder="Password"
              onChange={(e) => {
                setPassword(e.target.value);
                checkPasswordStrength(e.target.value);
              }}
            />
          </Form.Item>

          {password && passwordProgress()}

          <Form.Item
            name="confirmPassword"
            dependencies={['password']}
            rules={[
              { required: true, message: 'Please confirm your password!' },
              ({ getFieldValue }) => ({
                validator(_, value) {
                  if (!value || getFieldValue('password') === value) {
                    return Promise.resolve();
                  }
                  return Promise.reject(new Error('Passwords do not match!'));
                },
              }),
            ]}
          >
            <Input.Password
              prefix={<LockOutlined />}
              placeholder="Confirm Password"
            />
          </Form.Item>

          <Form.Item
            name="first_name"
            rules={[{ required: true, message: 'Please input your Firstname!' }]}
          >
            <Input placeholder="Firstname" />
          </Form.Item>

          <Form.Item
            name="last_name"
            rules={[{ required: true, message: 'Please input your Lastname!' }]}
          >
            <Input placeholder="Lastname" />
          </Form.Item>

          <Form.Item>
            <Button type="primary" htmlType="submit">
              Register
            </Button>
          </Form.Item>
        </Form>
      </Modal>
    </>
  );
}

export default Register;