import React, { useState } from 'react';
import { Card, List, Tabs, Spin } from 'antd';
import './Home.css';
import default_img from "../img/default.jpg";
import CardTitle from './CardTitle'; // Import the new component

const { TabPane } = Tabs;
const tabKeys = {
  Streams: 'stream',
  Videos: 'videos',
  Clips: 'clips',
};

// Default thumbnail image URL
const DEFAULT_THUMBNAIL = default_img;

const processUrl = (url) => {
  // Check if URL contains '_404'
  if (url && url.includes('_404')) {
    return DEFAULT_THUMBNAIL;
  }
  
  // Process normal URL
  return url
    .replace('%{height}', '252')
    .replace('%{width}', '480')
    .replace('{height}', '252')
    .replace('{width}', '480');
};

const renderCardGrid = (data, loggedIn, favs, favOnChange, isLoading) => {
  if (isLoading) {
    return (
      <div style={{ textAlign: 'center', padding: '50px' }}>
        <Spin size="large" />
      </div>
    );
  }

  return (
    <List
      grid={{
        gutter: 16,
        xs: 1,
        sm: 2,
        md: 4,
        lg: 4,
        xl: 4,
      }}
      dataSource={data}
      renderItem={(item) => (
        <List.Item>
          <a
            href={item.url}
            target="_blank"
            rel="noopener noreferrer"
            style={{ display: 'block', height: '100%' }}
          >
            <Card
              title={<CardTitle item={item} loggedIn={loggedIn} favs={favs} favOnChange={favOnChange} />}
              style={{
                height: '100%',
                width: '100%',
              }}
              bodyStyle={{ padding: 0 }}
            >
              <img
                alt={item.title || 'Twitch content'}
                src={processUrl(item.thumbnail_url)}
                style={{
                  width: '100%',
                  height: '200px',
                  objectFit: 'cover',
                }}
                onError={(e) => {
                  e.target.src = DEFAULT_THUMBNAIL;
                }}
              />
            </Card>
          </a>
        </List.Item>
      )}
    />
  );
};

const Home = ({ resources, loggedIn, favoriteItems, favoriteOnChange }) => {
  const { videos, streams, clips } = resources;
  const { videos: favVideos, streams: favStreams, clips: favClips } = favoriteItems;
  const [isLoading, setIsLoading] = useState(false);

  const handleTabChange = (key) => {
    setIsLoading(true);
    setTimeout(() => {
      setIsLoading(false);
    }, 1000);
  };

  return (
    <Tabs defaultActiveKey={tabKeys.Streams} onChange={handleTabChange} forceRender={true}>
      <TabPane tab="Streams" key={tabKeys.Streams} forceRender={true}>
        {renderCardGrid(streams, loggedIn, favStreams, favoriteOnChange, isLoading)}
      </TabPane>
      <TabPane tab="Videos" key={tabKeys.Videos} forceRender={true}>
        {renderCardGrid(videos, loggedIn, favVideos, favoriteOnChange, isLoading)}
      </TabPane>
      <TabPane tab="Clips" key={tabKeys.Clips} forceRender={true}>
        {renderCardGrid(clips, loggedIn, favClips, favoriteOnChange, isLoading)}
      </TabPane>
    </Tabs>
  );
};

export default Home;